import React, { Component } from "react";
import 'abortcontroller-polyfill/dist/abortcontroller-polyfill-only';
import { indexServices } from "../services/index";


const abortController = new AbortController();

class Users extends Component {
  state = {
    githubUsers: null,
  };

  componentWillMount() {
    this.getUser();
  }
  getUser = async () => {
    const res = await indexServices.getUsers({ signal: abortController.signal });
    this.setState({
      githubUsers: res ? res.data.results : []
    }).catch(error => {
      if (error.name === 'AbortError') return; 
      throw error; 
    });
  };

  componentWillUnmount() {
    this.abortController.abort();
  }

  render() {
    console.log(this.state.githubUsers, '--')
    return (
      <>
        <input type="button" value="Show" onClick={this.getUser} />

        <div>
          {this.state.githubUsers &&
            this.state.githubUsers.map((value, key) => (
              <h2 key={value.id}>name : {value.email}</h2>
            ))}
        </div>
      </>
    );
  }
}
export default Users;
